<template>
  <div  id="create-variations">
    <div class="jumbotron">
      <h4 class="display-5">Quantities, pricing and shipping information for product {{product.title}}</h4>
      <hr>
      <div class="row">
          <div v-if="!variants.length > 0" class="col"><span>Variations</span></div>
          <div class="col" v-for="variant in variants" v-bind:key="variant"><span class="text-capitalize">{{ variant }}</span></div>
          <div v-bind:class="priceColSize + ' text-center'"><span>Price Per Unit</span></div>
          <div v-bind:class="varColSize + ' text-center'"><span>UK Shipping Cost</span></div>
          <div v-bind:class="varColSize + ' text-center'"><span>Overseas Shipping Cost</span></div>
          <div v-bind:class="varColSize + ' text-center'"><span>Qty Available</span></div>
          <div v-bind:class="varColSize + ' text-center'"><span>Qty per Shipment</span></div>
          <div class="col-1 text-center"><span>Inactive</span></div>
      </div>
      <div v-for="(variationLine, index) in variationsMatrix" v-bind:key="index" v-bind:class="inactiveStyle(variationsMatrix[index].inactive) + ' row'" >
            <div style="margin-top:0.5em;"  class="col" v-for="(variantValue, variant) in variationLine" v-if="variants.includes(variant)" v-bind:key="variantValue">
              <span class="text-capitalize border border-top-0 border-left-0 border-right-0 border-secondary">{{ variantValue }}</span>                        
            </div>
            <div v-bind:class="priceColSize + ' input-group'">
              <span style="margin-top:0.5em; margin-right:0.1em;">£</span>
              <input type="text" v-model="variationsMatrix[index].unitPrice" name="unitPrice" class="form-control" v-bind:id="'unitPrice-'+index">                        
            </div>
            <div v-bind:class="varColSize + ' input-group'">
              <span style="margin-top:0.5em; margin-right:0.1em;">£</span>
              <input type="text" v-model="variationsMatrix[index].shippingCostUk" name="shippingCostUk" class="form-control" v-bind:id="'shippingCostUk-'+index">                        
            </div>
            <div v-bind:class="varColSize + ' input-group'">
              <span style="margin-top:0.7em; margin-right:0.1em;">£</span>
              <input type="text" v-model="variationsMatrix[index].shippingCostIntl" name="shippingCostIntl" class="form-control" v-bind:id="'shippingCostIntl-'+index">                        
            </div>
            <div v-bind:class="varColSize">
              <input type="text" v-model="variationsMatrix[index].quantity" name="quantity" class="form-control" v-bind:id="'quantity-'+index">                        
            </div>
            <div v-bind:class="varColSize">
              <input type="text" v-model="variationsMatrix[index].shipmentSize" name="shipmentSize" class="form-control" v-bind:id="'shipmentSize-'+index">                        
            </div>
            <div class="col-1">
              <input type="checkbox" style="margin-top:0.5em;" v-model="variationsMatrix[index].inactive" name="inactive" class="form-control" v-bind:id="'inactive-'+index">                        
            </div>
      </div>
    </div>
    <button v-on:click="createProduct()" type="button" class="btn btn-primary btn-block">Update</button>
  </div>
</template>

<script>
  import App from '../App'
  import utils from './utils'
  import matrix from './matrix'

  export default {
    name: 'create-variations',
    props: ['product'],
    data () {
      return {
        variants: [],
        variationsMatrix: {}
      }
    },
    computed: {
      priceColSize: function () {
        return this.variants.length > 7 ? 'col small' : 'col'
      },
      varColSize: function () {
        return this.variants.length > 4 ? 'col-1 small' : 'col'
      }
    },
    methods: {
      inactiveStyle: function (inactive) {
        return inactive ? 'bg-danger' : ''
      },
      loadVariations: function () {
        let vm = this
        App.bus.$on('add-variation', function ([variant, variantValue]) {
          let variationsMatrix = vm.addToMatrix(variant, variantValue, vm.variationsMatrix)
          vm.variationsMatrix = Object.assign({}, variationsMatrix)
          if (vm.variants && !vm.variants.includes(variant)) {
            vm.variants.push(variant)
          } else {
            vm.variants = vm.variants || [variant]
          }
        })
        App.bus.$on('remove-variation', function ([variant, variantValue]) {
          let [variationsMatrix, variants] = vm.removeFromMatrix(variant, variantValue, vm.variationsMatrix, vm.variants)
          vm.variationsMatrix = Object.assign({}, variationsMatrix)
          vm.variants = variants.slice()
        })
      },
      addToMatrix: matrix.addToMatrix,
      removeFromMatrix: matrix.removeFromMatrix,
      createProduct: function () {
        let productVariations = []
        Object.entries(this.variationsMatrix).forEach(([index, variationLine]) => {
          productVariations.push(variationLine)
          this.product.variations = productVariations
        })
        console.log('this is the final product' + JSON.stringify(this.product))
        utils.postData('product', this.product)
          .then(res => {
            if (res.ok) {
              console.log('Sucessfully created product and variations')
            } else {
              throw new Error(res.message)
            }
          })
          .catch(err => {
            console.log('We encountered a problem trying to create the product \n' + err)
          })
      }
    },
    beforeMount () {
      this.loadVariations()
    }
  }
</script>


