export default {
  addToMatrix: (variant, variantValue, variationsMatrix) => {
    if (Object.keys(variationsMatrix).length > 0) {
      Object.entries(variationsMatrix).forEach(([rowKey, rowValue]) => {
        if (rowValue.hasOwnProperty(variant)) {
          let clonedRow = Object.assign({}, rowValue)
          clonedRow[variant] = variantValue
          let cloneKey = rowKey.replace(rowValue[variant], variantValue)
          variationsMatrix[cloneKey] = clonedRow
        } else {
          rowValue[variant] = variantValue
          variationsMatrix[rowKey + '-' + variantValue] = rowValue
          delete variationsMatrix[rowKey]
        }
      })
    } else {
      let matrixRow = {}
      matrixRow[variant] = variantValue
      variationsMatrix[variantValue] = matrixRow
    }
    return variationsMatrix
  },
  removeFromMatrix: (variant, variantValue, variationsMatrix, variantList) => {
    let variantValues = []
    Object.entries(variationsMatrix)
      .forEach(([rowKey, rowValue]) => {
        if (!variantValues.includes(rowValue[variant])) {
          variantValues.push(rowValue[variant])
        }
      })
    if (variantValues.length > 1 || variantList.length === 1) {
      Object.entries(variationsMatrix)
        .filter(([rowKey, rowValue]) => rowValue[variant] === variantValue)
        .forEach(([rowKey, rowValue]) => delete variationsMatrix[rowKey])
      variantList = Object.keys(variationsMatrix).length > 0 ? variantList : []
    } else {
      Object.entries(variationsMatrix)
        .filter(([rowKey, rowValue]) => rowValue[variant] === variantValue)
        .forEach(([rowKey, rowValue]) => {
          const searchKey = rowKey.includes('-' + rowValue[variant]) ? '-' + rowValue[variant] : rowValue[variant] + '-'
          let newKey = rowKey.replace(searchKey, '')
          delete rowValue[variant]
          delete variationsMatrix[rowKey]
          variationsMatrix[newKey] = rowValue
        })
      variantList.splice(variantList.indexOf(variant), 1)
    }
    return [variationsMatrix, variantList]
  }
}



