<template>
    <div id="app" class="content">
      <Product />
    </div>
</template>

<script>
  import Vue from 'vue'
  import Product from './components/product'

  const bus = new Vue()

  export default {
    name: 'app',
    bus: bus,
    components: {
      Product
    }
  }
</script>



