<template>
  <div id="select-variations" class="jumbotron">
    <div class="row">
      <div class="col-6"><h4 class="display-5">Select variations</h4><hr></div>
      <div class="col-6"><h4 class="display-5">Variations selected</h4><hr></div>
    </div>              
    <div class="row">
        <div class="col-3">
            <input v-model="variant" list="variants" name="variant"
                type="search" class="form-control" id="variant" placeholder="variant" v-on:keyup.enter="focusToVariantValue()" >
        </div>
        <div class="col-3">
            <input ref="variantValue" v-model="variantValue" v-on:keyup.enter="addVariation(variant, variantValue)" type="search" name="variantValue" class="form-control" id="variant-value">
        </div>
        <div class="col-6">
            <div v-for="(variantValues, variant) in variations" v-bind:key="variant">
                <div v-for="(variantValue, index) in variantValues" v-bind:key="variant-index" class="row">
                    <div class="col-4">
                        <span class="text-capitalize">{{ variant }}</span>
                    </div>
                    <div class="col-4">
                        <span class="text-capitalize">{{ variantValue }}</span>
                    </div>
                    <div class="col-1">
                        <button v-on:click="removeVariation(variant, variantValue, index)" type="button" class="btn btn-danger btn-sm">
                            <i class="fa fa-trash-o" aria-hidden="true"></i>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <datalist id="variants">
        <option v-for="variant in variants" v-bind:key="variant" v-bind:value="variant"></option>
    </datalist>
  </div>
</template>

<script>
  import App from '../App'
  import utils from './utils'
  import config from './config'

  export default {
    name: 'select-variations',
    data () {
      return {
        variants: [],
        variant: '',
        variantValue: '',
        variations: {}
      }
    },
    methods: {
      loadVariants: function () {
        utils.fetchData(config.services.variants.path)
          .then(res => {
            this.variants = res
          })
      },
      focusToVariantValue: function () {
        if (this.variant) {
          this.$refs.variantValue.focus()
        }
      },
      addVariation: function (variant, variantValue) {
        let variations = this.variations
        if (!variations[variant]) {
          variations[variant] = []
        }
        if (variantValue && !variations[variant].includes(variantValue)) {
          variations[variant].push(variantValue)
          App.bus.$emit('add-variation', [variant, variantValue])
          this.variantValue = ''
        }
      },
      removeVariation: function (variant, variantValue, index) {
        if (this.variations[variant].includes(variantValue)) {
          this.variations[variant].splice(index, 1)
          const variantList = Object.assign({}, this.variations)
          this.variations = variantList
          App.bus.$emit('remove-variation', [variant, variantValue])
        }
      }
    },
    beforeMount () {
      this.loadVariants()
    }
  }
</script>


