 import config from './config.js'
 export default {
   fetchData: path => {
     return fetch(`${config.services.url}/${path}`)
      .then(res => res.json())
      .catch(err => err)
   },
   postData: (path, body) => {
     const headers = {
       'Content-Type': 'application/json'
     }
     let options = {
       method: 'POST',
       redirect: 'follow',
       follow: 10,
       timeout: 10000,
       body: JSON.stringify(body),
       headers: headers
     }
     return fetch(`${config.services.url}/${path}`, options)
         .then(res => res)
         .catch(err => err)
   }
 }



