<template>
    <div id="product" >
        <div class="jumbotron">
            <h4 class="item-details display-5">Item details</h4><hr>
            <div class="form-group row">
                <label for="inputTitle" class="col-sm-2 col-form-label">Title*</label>
                <div class="col-sm-4">
                    <input v-model="product.title" type="text" class="form-control" id="inputTitle">
                </div>
            </div>
            <div class="form-group row">
                <label for="selectCategory" class="col-sm-2 col-form-label">Category*</label>
                <div class="col-sm-4">
                    <select v-model="subCategories" v-on:click="updateProduct($event.target)" name="category" class="form-control">
                        <option selected="selected">Select category</option>
                        <option v-for="(subCategories, category) in categories" v-bind:key="category" v-bind:value='subCategories'>{{ category }}</option>
                    </select>
                </div>
            </div>
            <div class="form-group row">
                <label for="selectSubCategory" class="col-sm-2 col-form-label">Sub Category*</label>
                <div class="col-sm-4">
                    <select v-model="subSubCategories" v-on:click="updateProduct($event.target)" name="subCategory" class="form-control">
                        <option selected="selected">Select sub category</option>
                        <option v-for="(subSubCategories, subCategory) in subCategories" v-bind:key="subCategory" v-bind:value='subSubCategories'>{{ subCategory }}</option>
                    </select>
                </div>
            </div>
            <div class="form-group row">
                <label for="selectSubSubCategory" class="col-sm-2 col-form-label">Sub-sub Category*</label>
                <div class="col-sm-4">
                    <select v-model="subSubCategory" v-on:click="updateProduct($event.target)" name="subSubCategory" class="form-control">
                        <option selected="selected">Select sub sub category</option>
                        <option v-for="subSubCategory in subSubCategories" v-bind:key="subSubCategory"  v-bind:value='subSubCategory'>{{ subSubCategory }}</option>
                    </select>
                </div>
            </div>
            <div class="form-group row">
                <label for="inputItemOverview" class="col-sm-2 col-form-label">Item Overview</label>
                <div class="col-sm-8">
                    <textarea v-model="product.overview" class="form-control" rows="5" id="inputItemOverview"></textarea>
                </div>
            </div>
            <div class="form-group row">
                <label for="inputItemDescription" class="col-sm-2 col-form-label">Item Description*</label>
                <div class="col-sm-8">
                    <textarea v-model="product.description" class="form-control" rows="5" id="inputItemDescription"></textarea>
                </div>
            </div>
        </div>
        <CreateVariation />
        <ListVariation v-bind:product="product" />
    </div>
</template>

<script>
import utils from './utils'
import config from './config.js'
import CreateVariation from './select-variations'
import ListVariation from './create-variations'
export default {
  name: 'product',
  components: {
    CreateVariation,
    ListVariation
  },
  data () {
    return {
      title: '',
      categories: {},
      subCategories: 'Select category',
      subSubCategories: 'Select sub category',
      subSubCategory: 'Select sub sub-category',
      product: {}
    }
  },
  methods: {
    loadCategories: function () {
      utils.fetchData(config.services.refdata.path)
        .then(data => {
          this.categories = data
        })
    },
    updateProduct: function (target) {
      let selectedOption = target.options[target.selectedIndex]
      this.$set(this.product, target.name, selectedOption.text)
    }
  },
  beforeMount () {
    this.loadCategories()
  }
}
</script>



